import { EventEmitter, Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Subject } from 'rxjs';
import { Character } from './character.model';
import { map, tap } from 'rxjs/operators';

@Injectable({
    providedIn: 'root'
})
export class CharacterService {
    characters: Character[] = [];
    characterSelectedEvent = new EventEmitter<Character>();
    characterListChangedEvent = new Subject<Character[]>();
    newChar_id: string;
    
    constructor(private http: HttpClient) {
    }

    getCharacters() {
        this.http
            .get<{message: string, characters: any}>(
                'http://tylerportfolio.com:3000/api/characters'
            )
            .pipe(map((characterData) => {
                return characterData.characters.map(character => {
                    return {
                        name: character.name,
                        weaponType: character.weaponType,
                        element: character.element,
                        _id: character._id
                    };
                });
            }))
            .subscribe(characters => {
                this.characters = characters;
                this.characterListChangedEvent.next([...this.characters.sort((a, b) => a.name > b.name ? 1: b.name > a.name ? -1 : 0).slice()]);
            });
    }

    getCharacter(_id: string) {
        return this.http.get<{ message: string, character: Character }>("http://tylerportfolio.com:3000/api/characters/" + _id);
    }

    deleteCharacter(characterId: string) {
        this.http.delete("http://tylerportfolio.com:3000/api/characters/" + characterId)
            .subscribe(() => {
                this.characters = this.characters.filter(character => character._id !== characterId);
                this.characterListChangedEvent.next([...this.characters.sort((a, b) => a.name > b.name ? 1: b.name > a.name ? -1 : 0).slice()]);
            });
    }

    addCharacter(newCharacter: Character): any {
        if (!newCharacter) {
            return;
        }
        return this.http
            .post<{message: string, createdCharacter: Character}>("http://tylerportfolio.com:3000/api/characters", newCharacter)
            .pipe(tap((responseData) => {
                this.characters.push(responseData.createdCharacter);
                this.characterListChangedEvent.next([...this.characters.sort((a, b) => a.name > b.name ? 1: b.name > a.name ? -1 : 0)]);
            }));
            // .subscribe((responseData) => {
            //     console.log("Created Character: " + responseData.createdCharacter._id);
            //     console.log("Message: " + responseData.message);
            //     this.characters.push(responseData.createdCharacter);
            //     console.log(responseData.createdCharacter);
            //     console.log(this.characters);
            //     this.characterListChangedEvent.next([...this.characters.sort((a, b) => a.name > b.name ? 1: b.name > a.name ? -1 : 0)]);
            //     return responseData.createdCharacter._id;
            // });
    }

    updateCharacter(character: Character) {
        this.http
            .put("http://tylerportfolio.com:3000/api/characters/" + character._id, character)
            .subscribe(response => {
                const updatedCharacters = [...this.characters];
                const oldCharacterIndex = updatedCharacters.findIndex(c => c._id === character._id);
                updatedCharacters[oldCharacterIndex] = character;
                this.characters = updatedCharacters;
                this.characterListChangedEvent.next([...this.characters.sort((a, b) => a.name > b.name ? 1: b.name > a.name ? -1 : 0).slice()]);
            });

    }

}