import { HttpClient, JsonpClientBackend } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Ascension } from './ascension.model';
import { Character } from './character.model';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root',
})
export class AscensionService {
  ascensions: Ascension[] = [];

  constructor(private http: HttpClient) {}

  deleteAscensionByCharId(char_id: string) {
    console.log("Deleting ascensions!");
    this.http.delete("http://tylerportfolio.com:3000/api/ascensions/character/" + char_id).subscribe(() => {

    });
  }

  getAscension(_id: string): any {
     this.http.get<{ message: string, ascension: Ascension}>("http://tylerportfolio.com:3000/api/ascensions");
    
  }

  getAscensionByCharId(char_id: string) {
    return this.http.get<{message: string, ascensions: Ascension[]}>("http://tylerportfolio.com:3000/api/ascensions/character/" + char_id);
  }

  addAscension(newAscension: Ascension) {
    if (!newAscension) {
      return;
    }
    console.log(newAscension);

    this.http
      .post<{ message: string; createdAscension: Ascension }>(
        'http://tylerportfolio.com:3000/api/ascensions',
        newAscension
      )
      .subscribe((responseData) => {
        this.ascensions.push(responseData.createdAscension);
      });
  }

  updateAscension(ascension: Ascension){
    this.http
      .put("http://tylerportfolio.com:3000/api/ascensions" + ascension._id, ascension)
      .subscribe(response => {
        const updatedAscensions = [...this.ascensions];
        const oldAscensionIndex = updatedAscensions.findIndex(a => a._id === ascension._id);
        updatedAscensions[oldAscensionIndex] = ascension;
        this.ascensions = updatedAscensions;
      });
  }
}
