import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Leveling } from "./leveling.model";
import { map } from 'rxjs/operators';
import { Subject } from "rxjs";

@Injectable({
    providedIn: 'root',
})
export class LevelingService {
    leveling: Leveling[] = [];

    levelingLoadedEvent = new Subject<Leveling[]>();

    constructor(private http: HttpClient) {}



    getLevelingTable() {
        this.http
            .get<{message: string, leveling: any}>(
                'http://tylerportfolio.com:3000/api/leveling'
            )
            .pipe(map((levelingData) => {
                return levelingData.leveling.map(level => {
                    return {
                        _id: level._id,
                        level: level.level,
                        exp: level.exp,
                        cost: level.cost,
                        mat1t1: level.mat1t1,
                        mat1t2: level.mat1t2,
                        mat1t3: level.mat1t3,
                        mat1t4: level.mat1t4,
                        mat2: level.mat2,
                        mat3: level.mat3,
                        mat4t1: level.mat4t1,
                        mat4t2: level.mat4t2,
                        mat4t3: level.mat4t3
                    };
                });
            }))
            .subscribe(leveling => {
                this.leveling = leveling;
                this.levelingLoadedEvent.next([...this.leveling.slice()]);
            });
            
    }
}